import { writable } from "../../web_modules/svelte/store.js";
import { isClientSide } from "./common.js";

const defaultValue = [];
const { subscribe, set } = writable(defaultValue);
if (isClientSide) {
  const storageValue = JSON.parse(sessionStorage.getItem('cart'));
  if (storageValue) set(storageValue);
}

let cart;

subscribe(value => {
  cart = value;
});

const store = (value) => {
  sessionStorage.setItem('cart', JSON.stringify(value));
  set(value);
}


export default {
  subscribe,
  reset: () => store(defaultValue),
  set: store,
  add: (product) =>
    store([...cart, product])

};
